table {
    border-collapse: collapse;
    width: 100%;

    th {
        font-weight: 700;
        padding: 8px;
        text-align: left;
        border-bottom: 1px solid #ddd;
    }
    
    td {
        padding: 8px;
        text-align: left;
        border-bottom: 1px solid #ddd;
    }
    
    tr:hover {
        background-color: #f5f5f5;
    }
}
